* {
    padding: 0;
    margin: 0;
}

.show1 {
    margin:  0 auto;
    width: 500px;
}

.show2 {
    margin:  0 auto;
    width: 500px;
}

.inp {
    width: 500px;
    height: 30px;
    line-height:30px;
    border-left: 5px solid green;
    border-radius: 3px;
    margin-bottom: 10px;
    background: rgb(238, 253, 238);
}

.inped {
    width: 500px;
    height: 30px;
    line-height:30px;
    border: 1px solid grey;
    border-left: 5px solid black;
    border-radius: 3px;
    margin-bottom: 10px;
    background: rgb(99, 89, 89);
}

.ckbox {
    display: block;
    float: left;
    width: 20px;
    height: 20px;
    margin-top: 5px;
    margin-left: 10px;
    margin-right: 10px;
}

.but {
    display: block;
    float: right;
    color: white;
    width: 25px;
    height: 25px;
    margin-top: 2px;
    margin-right: 10px;
    border-radius: 12px;
    border: 2px solid white;
}